"use client";
import * as React from 'react';
import { createContext, useContext, useMemo, ReactNode } from 'react';
import { AIProvider as AIProviderInterface, ProviderId } from '../providers/types';
import { createOpenAIProvider } from '../providers/openai';
import { createGeminiProvider } from '../providers/gemini';

export interface AIProviderContextValue {
  provider: AIProviderInterface;
}

const AIContext = createContext<AIProviderContextValue | null>(null);

export interface AIProviderProps {
  children: ReactNode;
  provider?: ProviderId;
  apiKey?: string;
  options?: any;
}

export const AIProvider: React.FC<AIProviderProps> = ({ children, provider = 'openai', apiKey, options }) => {
  const prov = useMemo<AIProviderInterface>(() => {
    if (provider === 'openai') {
      if (!apiKey) throw new Error('OpenAI provider requires apiKey.');
      return createOpenAIProvider({ apiKey, ...options });
    }

    if (provider === 'gemini') {
      if (!apiKey) throw new Error('Gemini provider requires apiKey.');
      return createGeminiProvider({ apiKey, ...options });
    }

    throw new Error(`Unknown provider: ${provider}`);
  }, [provider, apiKey, JSON.stringify(options || {})]);

  return <AIContext.Provider value={{ provider: prov }}>{children}</AIContext.Provider>;
};

export function useAIProviderContext(): AIProviderContextValue {
  const ctx = useContext(AIContext);
  if (!ctx) throw new Error('useAIProviderContext must be used inside AIProvider');
  return ctx;
}
