import { AIProvider, Message } from './types';
import OpenAI from 'openai';

export interface OpenAIOptions {
  apiKey: string;
  baseUrl?: string; // optional custom base url
  model?: string;
  extraHeaders?: Record<string, string>; // e.g., HTTP-Referer, X-Title for OpenRouter rankings
  dangerouslyAllowBrowser?: boolean;
}

export function createOpenAIProvider(opts: OpenAIOptions): AIProvider {
  const baseURL = opts.baseUrl || 'https://openrouter.ai/api/v1';
  let model = opts.model || 'openai/gpt-4o-mini';
  const isProxy = typeof baseURL === 'string' && baseURL.startsWith('/');

  // If pointing to a same-origin proxy (e.g., "/api/openai"), avoid instantiating the browser SDK entirely.
  // That prevents browser-environment guards while keeping secrets server-side.
  const client = !isProxy
    ? new OpenAI({
        apiKey: opts.apiKey,
        baseURL,
        dangerouslyAllowBrowser: opts.dangerouslyAllowBrowser ?? true,
      })
    : null;

  async function chat(messages: Message[]): Promise<Message[]> {
    // Allow the caller to send a system message like: {role:'system', content:'model: anthropic/claude-3.5-sonnet'}
    const sys = messages.find(m => m.role === 'system' && typeof m.content === 'string' && (m.content as string).startsWith('model:'));
    if (sys) {
      const parts = (sys.content as string).split(':');
      if (parts[1]) model = parts.slice(1).join(':').trim();
    }
    if (!isProxy && client) {
      const completion = await client.chat.completions.create({
        model,
        messages: messages.map(m => ({ role: m.role as any, content: m.content })),
        extra_headers: opts.extraHeaders,
      } as any);

      const out: Message[] = [];
      if (completion.choices && completion.choices.length) {
        for (const ch of completion.choices) {
          const content = ch.message?.content;
          if (content) {
            out.push({ role: (ch.message?.role as any) || 'assistant', content });
          }
        }
      }
      return out;
    }

    // Proxy path: call OpenAI-compatible route without exposing keys
    const resp = await fetch(`${baseURL}/chat/completions`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        ...(opts.extraHeaders || {}),
      },
      body: JSON.stringify({ model, messages }),
    });
    if (!resp.ok) {
      const text = await resp.text();
      throw new Error(`OpenAI chat (proxy) error: ${resp.status} ${text}`);
    }
    const data = await resp.json();
    const out: Message[] = [];
    if (data.choices && data.choices.length) {
      for (const ch of data.choices) {
        const content = ch.message?.content;
        if (content) out.push({ role: ch.message?.role || 'assistant', content });
      }
    }
    return out;
  }

  async function generateImage(prompt: string): Promise<string> {
    // When using proxy, do not send Authorization; let server add secrets
    const headers: Record<string, string> = {
      'Content-Type': 'application/json',
      ...(opts.extraHeaders || {}),
    };
    if (!isProxy) {
      headers['Authorization'] = `Bearer ${opts.apiKey}`;
    }
    const resp = await fetch(`${baseURL}/images/generations`, {
      method: 'POST',
      headers,
      body: JSON.stringify({ prompt, n: 1, size: '1024x1024' }),
    });
    if (!resp.ok) {
      const text = await resp.text();
      throw new Error(`OpenAI image error: ${resp.status} ${text}`);
    }
    const data = await resp.json();
    if (data.data && data.data[0]) {
      if (data.data[0].url) return data.data[0].url;
      if (data.data[0].b64_json) {
        const raw: string = data.data[0].b64_json;
        return raw.startsWith('data:') ? raw : `data:image/png;base64,${raw}`;
      }
    }
    throw new Error('OpenAI image: unexpected response');
  }

  return {
    id: 'openai',
    chat,
    generateImage,
  };
}
