"use client";
import { useCallback, useState } from 'react';
import { Message, Role } from '../providers/types';
import { useAIProviderContext } from '../context/AIProviderContext';

export function useAI(initialSystemPrompt?: string) {
  const { provider } = useAIProviderContext();
  const [messages, setMessages] = useState<Message[]>(
    initialSystemPrompt 
      ? [{ role: 'system' as Role, content: initialSystemPrompt }] 
      : []
  );
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<Error | null>(null);

  const sendMessage = useCallback(async (content: string) => {
    setLoading(true);
    setError(null);
    const next: Message[] = [...messages, { role: 'user' as Role, content }];
    setMessages(next);
    try {
      const replies = await provider.chat(next);
      const merged: Message[] = [...next, ...replies];
      setMessages(merged);
      setLoading(false);
      return replies;
    } catch (e) {
      setError(e as Error);
      setLoading(false);
      throw e;
    }
  }, [provider, messages]);

  return { messages, sendMessage, loading, error, setMessages };
}
