import { AIProvider as AIProviderInterface, Message } from "./types";
import { GoogleGenerativeAI } from "@google/generative-ai";

export function createGeminiProvider({ apiKey, model = "gemini-1.5-pro", ...options }: { apiKey: string; model?: string; [key: string]: any }): AIProviderInterface {
  if (!apiKey) {
    throw new Error("Gemini provider requires apiKey.");
  }

  const genAI = new GoogleGenerativeAI(apiKey);
  const modelInstance = genAI.getGenerativeModel({ model });

  return {
    id: "gemini",
    chat: async (messages: Message[]) => {
      // Convert messages to Gemini-compatible format
      const contents = messages.map((m) => {
        let role = m.role;

        // Gemini does not support "system" role â†’ treat it as "user"
        if (role === "system") role = "user";
        if (role === "assistant") role = "model"; // Gemini expects "model"

        return {
          role,
          parts: [{ text: m.content }],
        };
      });

      const result = await modelInstance.generateContent({ contents });

      const text = result.response.text();

      return [
        {
          role: "assistant",
          content: text,
        },
      ];
    },
  };
}
