"use client";
import { useState, useCallback } from 'react';
import { useAIProviderContext } from '../context/AIProviderContext';

export function useImageAI() {
  const { provider } = useAIProviderContext();
  const [image, setImage] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<Error | null>(null);

  const generateImage = useCallback(async (prompt: string) => {
    if (!provider.generateImage) throw new Error('Provider does not support image generation');
    setLoading(true);
    setError(null);
    try {
      const url = await provider.generateImage(prompt);
      setImage(url);
      setLoading(false);
      return url;
    } catch (e) {
      setError(e as Error);
      setLoading(false);
      throw e;
    }
  }, [provider]);

  return { image, generateImage, loading, error, setImage };
}
